@import url("https://fonts.googleapis.com/css2?family=Caveat&family=Merriweather:wght@300;400&display=swap");

.App {
  font-family: "Merriweather", serif;
  text-align: center;
  display: flex;
  flex-wrap: unset;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #252525;
  overflow: hidden;
}
a {
  cursor: pointer;
  text-decoration: none;
}



